<template>
  <div class="personal">
    <div class="main">
      <div class="content">
        <div class="controller-title">个人中心</div>
        <div class="inner">
          <span>昵称：</span>
          <el-input placeholder="用户名" v-model.trim="userInfo.username" maxlength="6"></el-input>
        </div>
        <div class="inner">
          <span>手机：</span>
          <el-input placeholder="用户名" v-model.trim="userInfo.phone" disabled></el-input>
        </div>
        <div class="inner">
          <span>真实姓名：</span>
          <el-input placeholder="用户名" v-model.trim="userInfo.true_name" maxlength="4"></el-input>
        </div>
        <div class="inner">
          <span>生日：</span>
          <el-date-picker v-model="userInfo.birthday" type="date" placeholder="选择日期" value-format="yyyy-MM-dd">
          </el-date-picker>
        </div>
        <div class="inner">
          <span>性别：</span>
          <el-radio v-model="userInfo.gender" label="男">男</el-radio>
          <el-radio v-model="userInfo.gender" label="女">女</el-radio>
        </div>
        <div class="inner">
          <span>邮箱：</span>
          <el-input v-model.trim="userInfo.email" placeholder="邮箱"></el-input>
        </div>

        <div class="inner">
          <span>个人简介：</span>
          <el-input v-model.trim="userInfo.personal_brief" placeholder="个人简介" maxlength="30"></el-input>
        </div>
        <el-button type="primary" @click="changeUserInfo" :disabled="is_save">保存</el-button>
        <div class="avatar">
          <img src="../../assets/img/avatar.png" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Personal',
    data() {
      return {
        url: '/user',
        userInfo: {},
        is_save: false
      }
    },
    created() {
      this.getUserInfo();
    },
    methods: {
      // 获取用户信息 根据 phone
      getUserInfo: function() {
        if (this.$store.state.phone_reg.test(this.$store.state.login_phone)) {
          this.$axios.get(this.url + '/queryUserInfoByPhone/' + this.$store.state.login_phone, {
              headers: {
                'token': window.localStorage.getItem('token')
              }
            })
            .then(res => {
              if (res.data.code == 200) {
                this.userInfo = res.data.body;
              } else {
                this.$message({
                  message: res.data.message,
                  type: 'warning'
                })
              }
            })
        } else {
          // 跳转登录
          this.$router.push('/login');
        }
      },
      // 更新用户信息
      changeUserInfo: function() {
        if (this.$store.state.email_reg.test(this.userInfo.email)) {
          this.$axios.post(this.url + '/updateUserByPhone', this.userInfo).then(res => {
            res.data.code == 200 ? this.$message({
              message: res.data.message,
              type: 'success'
            }) : this.$message({
              message: res.data.message,
              type: 'warning'
            })
          })
        } else {
          this.$message({
            message: '邮箱一定要填，后续找回密码需要这个',
            type: 'warning'
          })
        }
      }
    }
  }
</script>

<style scoped>
  .content {
    position: relative;
  }

  .el-input {
    width: 30%;
  }

  .el-button {
    margin-left: 50px;
  }

  .inner {
    margin-bottom: 40px;
    padding-left: 50px;
  }

  .inner>span {
    display: inline-block;
    width: 80px;
  }

  .inner:nth-child(8) .el-input {
    width: 60%;
  }

  .avatar {
    width: 150px;
    height: 150px;
    position: absolute;
    top: 20px;
    right: 120px;
    border-radius: 50%;
    overflow: hidden;
  }

  .avatar img {
    width: 100%;
    height: 100%;
  }
</style>



